.orderContainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  margin-bottom: 30px;
}
@media (max-width: 620px) {
  .orderContainer {
    margin-bottom: 15px;
  }
}

.address {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}

.orderContactsHeader {
  font-size: clamp(0.8rem, 0.2rem + 1vw, 1rem);
  text-align: center;
  margin-bottom: 30px;
}
@media (max-width: 620px) {
  .orderContactsHeader {
    margin-bottom: 15px;
  }
}

.contactNumber {
  display: inline-block;
  background-color: inherit;
  color: inherit;
  text-decoration: none;
  margin-top: 5px;
  font-size: clamp(0.675rem, 0.2rem + 0.85vw, 0.875rem);
}

